    readyInMinutes:17,
    serving:6,
    summary:"A sized-up, super chocolatey twist on the classic American campfire treat. This s'mores dip will be gone before you can say 'mini marshmallow' ",
    Ingredients:["200g milk chocolate",'2 tbsp full-fat milk','350g mini marshmallows','100g digestive biscuits',"100g strawberries, washed and hulled",'100g pineapple chunks','skewers'],
