    readyInMinutes:50,
    serving:6,
    summary:"Serve this smoky, chunky veg gumbo dip with Tom Kerridge's Southern-fried prawns. It makes an excellent condiment for an American-themed platter ",
    Ingredients:["2 tbsp vegetable oil",'1 large onion, chopped','4 garlic cloves, crushed','1 tsp smoked paprika',"½ tsp cayenne pepper",'1 green pepper, finely chopped','½ x 400g can chopped tomatoes','100ml chicken or vegetable stock','1 bay leaf','4 spring onions, trimmed and sliced'],
    Instructions:["Heat the oil in a frying pan and sizzle the onion for 10-15 mins. Add the garlic, spices and green pepper and fry for 1 min more.","Tip in the tomatoes, stock and bay leaf, bring to the boil and simmer for 20 mins until thick. Season and stir in the spring onions. Leave to cool before serving. Will keep in an airtight container in the fridge for up to four days."],
